@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");
/* @import url('https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap'); */

* {
    box-sizing: border-box;
}

body {
    font-family: "Lato", sans-serif;
    /* background-color: #242333; */
    /* background: url("myke-simon-atsUqIm3wxo-unsplash.jpg"),
        linear-gradient(rgba(0, 0, 0, .9), rgba(0, 0, 0, 0.9)); */
    background:
        linear-gradient(rgba(0, 18, 50, 0.7), rgba(0, 0, 0, 0.7)),
        url("./lloyd-dirks-4SLz_RCk6kQ-unsplash.jpg") bottom;
    background-size: cover;
    color: #fff;
    min-height: 100vh;
    background-color: rgb(0, 18, 50);
    padding-top: 30px;
}

#root {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

.book-detail {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
}
.book-input {
    margin-bottom: 40px;
}

.book-input form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.book-input input {
    font-size: 1rem;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    max-width: 300px;
}

.book-input button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.book-input button:hover {
    background-color: #0056b3;
}

.movie-container {
    margin: 20px 0;
}

.movie-container select {
    background-color: #fff;
    font-family: "Lato", sans-serif;
    border: 0;
    border-radius: 5px;
    font-size: 18px;
    outline: none;
    margin-left: 10px;
    padding: 5px 15px 5px 15px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    /* Will remove the browser style and let you use your own style */
}

.container {
    /*  it simply enables a 3D-space for children elements. */
    perspective: 1000px;
    -webkit-perspective: 1000px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.row-container {
    margin-top: 30px;
}

.seat {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #444451;
    margin: 2px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

    height: 36px;
    width: 36px;
    background: #01163e;
    border: 2px solid #314c81;
    transition: all 0.2s ease;
}

.seat.selected {
    background-color: #6feaf6;
    background-color: #31d7a9;
}

.seat.occupied {
    background-color: #fff;
}
.seat.disabled {
    background-color: #333;
    color: #fff;
    cursor: not-allowed;
}
.seat:nth-of-type(2) {
    margin-right: 18px;
}

.seat:nth-last-of-type(2) {
    margin-left: 18px;
}

.seat:not(.occupied):hover {
    /* Selecting seats that do not have "occupied" class */
    cursor: pointer;
    transform: scale(1.2);
}

.showcase .seat:not(.occupied):hover {
    cursor: default;
    transform: scale(1);
}

.showcase {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 5px 10px;
    border-radius: 5px;
    color: #777;
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}

.showcase li {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.showcase li small {
    margin-left: 2px;
}

.row {
    display: flex;
}

.screen {
    background-color: #fff;
    height: 70px;
    width: 100%;
    margin: 15px 0;
    transform: rotateX(-45deg);
    box-shadow: 0 3px 10px rgba(255, 255, 255, 0.7);
}

p.text {
    margin: 5px 0;
}

p.text span {
    color: #6feaf5;
}

.movie-screen {
    margin-top: 0px;
    margin-bottom: 35px;
    text-align: center;
}

.movie-screen img {
    max-width: 85%;
    opacity: 1;
}

.subtitle {
    position: relative;
    text-align: center;
    font-weight: 600;
    text-transform: uppercase;
    padding: 10px;
    color: #31d7a9;
    font-size: 16px;
    max-width: 270px;
    margin-left: auto;
    margin-right: auto;
}

.subtitle:before,
.subtitle:after {
    position: absolute;
    content: "";
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    height: 1px;
    background: #213a69;
}

.subtitle:before {
    width: 80%;
    top: 0;
}

.subtitle:after {
    width: 100%;
    bottom: 0;
}

.text {
    font-size: 18px;
}

#count,
#total {
    font-size: 24px;
    padding: 0 8px;
}

.text-wrapper {
    margin-top: 10px;
}

@media (max-width: 576px) {
    body {
        padding-top: 30px;
    }

    .movie-container select {
        font-size: 14px;
    }

    .seat {
        height: 21px;
        width: 23px;
    }

    .movie-screen img {
        max-width: 92%;
    }

    .subtitle {
        font-size: 13px;
        padding: 7px;
    }

    .seat:nth-of-type(2) {
        margin-right: 12px;
    }

    .seat:nth-last-of-type(2) {
        margin-left: 12px;
    }

    .movie-screen {
        margin-top: 25px;
        margin-bottom: 20px;
    }

    .text {
        font-size: 14px;
    }

    #count,
    #total {
        font-size: 18px;
    }
}

.book-result {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    margin: auto;
    width: 35%;
}
.book-result-header {
    text-align: center;
    margin-bottom: 20px;
}

.book-result-customer-name {
    font-size: 22px;
    font-weight: bold;
    color: #fff;
    background-color: #000000;
    padding: 10px 20px;
    border-radius: 4px;
    display: inline-block;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.greeting {
    font-weight: normal;
    margin-right: 5px;
}

.book-result-table {
    border-collapse: collapse;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.book-result-table th,
.book-result-table td {
    border: 1px solid #444;
    padding: 12px;
    text-align: left;
}

.book-result-table th {
    background-color: #222;
    color: #fff;
    font-weight: bold;
}
.book-result-table th:first-child {
    width: 25%;
}
.book-result-table th:nth-child(2) {
    width: 50%;
}

.book-result-table tr:nth-child(even) {
    background-color: rgba(255, 255, 255, 0.2);
}
.book-result-table tr:last-child {
    background-color: rgba(49, 215, 169, 0.3);
}

.confirm-booking-button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.confirm-booking-button:hover {
    background-color: #0056b3;
}

.total-price {
    font-weight: bold;
    font-size: larger;
    background-color: #f0f0f0;
}

.total-price td {
    padding: 10px;
}

.currency {
    font-family: "Arial", sans-serif;
    font-weight: bold;
    margin-left: 5px;
}

.place-order-button {
    padding: 10px 20px;
    font-size: 15px;
    font-weight: bold;
    color: #fff;
    background-color: #ff004c;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 30px;
}
